body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#search {
    padding: 5px;
}

::-webkit-input-placeholder {
    font-size: calc(0.55em + 0.55vw);
  }

::-moz-placeholder {
    font-size: calc(0.55em + 0.55vw);
  }

::-ms-input-placeholder {
    font-size: calc(0.55em + 0.55vw);
  }

::placeholder {
    font-size: calc(0.55em + 0.55vw);
  }

.button {
    font-weight: bold;
}

#description {
    text-align: justify;
}

#title, #author, #description {
    font-size: calc(0.5em + 0.5vw);
}

img {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.columns {
    padding-top: 10px;
}
h1 {
    font-weight: bolder;
    color: rgb(78, 78, 78);
}

.navbar-item:hover {
    color: black;
}

#main {
    background-color: white;
}
.hero {
    background: url(/static/media/bookshelf.a28e76b8.jpg) no-repeat center center;
    background-size: cover;
    display: table;
    width: 100%;
    padding-top: 120px;
}
  
h1 {
      font-size: calc(1em + 1vw);
      text-align: center;
}
  
h2 {
      font-size: calc(0.5em + 1vw);
      text-align: center;
      color: rgb(78, 78, 78);
      font-weight: bold;
}

#google {
      color: rgb(78, 78, 78);
}
footer {
    height: 60px;
    padding-top: 15px;
    font-size: calc(8px + 8 * (100vw - 320px)/ 800);
    text-align: center;
    background-color: rgb(235, 235, 235);
}

a{
    color: black;
    font-weight: bolder;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

